<template>
  <div class="stack-small" v-if="!isEditing">
    <div class="custom-checkbox">
      <input
        type="checkbox"
        class="checkbox"
        :id="id"
        :checked="isDone"
        @change="$emit('checkbox-changed')" />
      <label :for="id" class="checkbox-label">{{label}}</label>
    </div>
    <div class="btn-group">
      <el-button :style="{ fontSize: '20px' }" type="primary" size="large" round class="btn" ref="editButton" @click="toggleToItemEditForm" >
        Edit<span class="visually-hidden">{{label}}</span>
      </el-button>
      <el-button :style="{ fontSize: '20px' }" type="danger" size="large" round class="btn btn__danger" @click="deleteToDo" >
        Delete <span class="visually-hidden">{{label}}</span>
      </el-button>
    </div>
  </div>
  <to-do-item-edit-form v-else :id="id" :label="label" @item-edited="itemEdited" @edit-cancelled="editCancelled"></to-do-item-edit-form>
</template>


<script>
import ToDoItemEditForm from "./ToDoItemEditForm";

export default {
  components: {
  ToDoItemEditForm
},
  props: {
    label: { required: true, type: String },
    done: { default: false, type: Boolean },
    id: { required: true, type: String },
    components: {
  ToDoItemEditForm
      },
  },
  data() {
    return {
      isEditing: false
    };
  },
  computed: {
  isDone() {
    return this.done;
  }
},
methods: {
    deleteToDo() {
      this.$emit('item-deleted');
    },
    toggleToItemEditForm() {
      console.log(this.$refs.editButton);
      this.isEditing = true;
    },
    itemEdited(newItemName) {
  this.$emit("item-edited", newItemName);
  this.isEditing = false;
  this.focusOnEditButton();
},
editCancelled() {
  this.isEditing = false;
  this.focusOnEditButton();
},

focusOnEditButton() {
  this.$nextTick(() => {
    const editButtonRef = this.$refs.editButton;
   if (editButtonRef && editButtonRef.$el) {
      // 调用原生 DOM 元素的 focus 方法
      editButtonRef.$el.focus();
    } else {
      console.error('editButtonRef is not a valid reference:', editButtonRef);
    }
  });
} }

};
</script>